import { message } from 'antd';

const axios = require('axios');

export const createPurchase = async({data:d,setSuccess,form})=>{
    try{
        let {data} = await axios.post('api/purchase',d)
        if(data.success===true){
            setSuccess(true)
            form.resetFields()
            message.success("Product has been purchased!")
        }else{
            message.error(data.message)
        }
        
    }catch(error){
        message.error(error.message)
    }
}