import React from 'react'
import { Modal, Form, Input, Row, Col, Button,Select, message, InputNumber} from 'antd';
import { API_LINK } from '../../../context/headerContext';
const { Option } = Select;

export default function AddProduct({open,setOpen,setSuccess}) {
    const axios = require('axios');
    let [form] = Form.useForm()

    const addProduct =async (e) =>{
        try{
            await axios.post(API_LINK+'/api/product', e)
            setSuccess(true)
            setOpen(false)
            form.resetFields()
        }catch(error){
            message.error(error.message)
        }
        
    }

    const onFinish = (values) => {
        addProduct(values)
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title="CREATE NEW PRODUCT"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addProduct"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="name"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <Input placeholder="name" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="category"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <Select placeholder="category" style={{ width: "100%" }}>
                                <Option value="VEGETABLE">VEGETABLE</Option>
                                <Option value="MEAT">MEAT</Option>
                                <Option value="FRUIT">FRUIT</Option>
                                <Option value="CAKE">CAKE</Option>
                                <Option value="EGG">EGG</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="inStock"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <InputNumber min={0} placeholder="in stock" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="price"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <InputNumber min={0} placeholder="price $"  style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }} >
                        <Form.Item
                            name="remark"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="remark" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                            // onClick={()=> }
                        >
                            CREATE
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
