import { message } from 'antd';

const axios = require('axios');

export const getPurchaseList = async({keyword,pageNumber,limit,setData,setLoading})=>{
    try{
        let {data} = await axios.get(`api/purchase?keyword=${keyword}&pageNumber=${pageNumber}&limit=${limit}`)
        setData(data)
        setLoading(false)
        
    }catch(error){
        message.error(error)
    }
}