import React from 'react'
import {Space,Popconfirm,Button,Input,Form} from 'antd'
import {DeleteOutlined  } from '@ant-design/icons'
import { currencyFormat, numberWithCommas } from '../../../fn/fn';
import { rielAsOne } from '../../../context/headerContext';

const ButtonGroup = Button.Group;

export default function orderColumn({handleRemoveOrder,handlePlusQty,handleMinusQty,handleEditQty}) {
    // let [form] = Form.useForm()
    var array = [
        {
            title: 'NÂ°',
            dataIndex: 'no',
            key: 'no',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: 'Item',
            dataIndex: 'name',
            key: 'name',
            width: 150,
        },
        {
            title: 'Quantity',
            dataIndex: 'qty',
            key: 'qty',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                   
                    {/* <ButtonGroup>
                        <Button onClick={()=> handleMinusQty(record)} disabled={record.qty ===1}>
                        -
                        </Button>
                        <div style={{padding:5,borderWidth:1}}> {record.qty} </div>
                        <Button onClick={()=> handlePlusQty(record)}>
                        +
                        </Button>
                    </ButtonGroup> */}

                            <Input type="number" min={0} onChange={(e)=> handleEditQty({...record,newValue:e.target.value})} value={record.qty} />
                </Space>
            ),
        },

        {
            title: 'Price',
            dataIndex: 'price',
            key: 'price',
            width: 100,
        },
        {
            title: 'Amount',
            dataIndex: 'amount',
            key: 'amount',
            width: 150,
            render: (text, record) => {
            
            return (
                <span size="middle">
                    {/* {currencyFormat(record.qty * record.price)} */}
                    {numberWithCommas((record?.qty * record?.price) * rielAsOne )}&#6107;
                </span>
            )
            }
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 50,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {/* <Link className="link" to={"/subCase/" + record.id}><EditOutlined /></Link> */}
                    <Popconfirm
                        title="Do you want to remove this item?"
                        onConfirm={() => handleRemoveOrder(record._id)}
                        okText="Yes"
                        cancelText="No"
                        style={{cursor:'pointer'}}
                    >
                        <Button className="link" style={{ color: "red" }}><DeleteOutlined /></Button>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}
