import { message } from 'antd';

const axios = require('axios');

export const editPurchase = async({editData,setSuccess})=>{
    try{
        let {data} = await axios.put(`api/purchase/${editData._id}`,editData)
        if(data.success===true){
            setSuccess(true)
            message.success("Purchase has been updated!")
        }else{
            message.error(data.message)
        }
        
    }catch(error){
        message.error(error)
    }
}