import React, { useEffect, useState } from 'react'
import {Row,Col,Button,Card,Table,Image,Input,message, Form,Select,DatePicker} from 'antd'
import purchaseColumn from './tableColumn/purchaseColumn';
import moment from 'moment';
import { createPurchase } from './create/createPurchase';
import { getPurchaseList } from './get/getPurchase';
import { deletePurchase } from './delete/deletePurchase';
import { editPurchase } from './update/updatePurchase';

const {Option} = Select

export default function Purchase({productData}) {
    let [form] = Form.useForm()
    const axios = require('axios');

    const [purchaseData,setPurchaseData] = useState({})
    const [open,setOpen] = useState(false)
    const [success,setSuccess] = useState(false)
    const [editData,setEditData] = useState({})

    const [loading ,setLoading]= useState(true)

    const [keyword ,setKeyword] = useState("")
    const [pageNumber,setPageNumber] = useState(0)
    const [limit,setLimit]= useState(15)

    const [isEdit,setIsEdit] = useState(false)

    const [selectedProduct, setSelectedProduct] = useState({})

    // const getPurchaseList = async()=>{
    //     try{
    //         let {data} = await axios.get('api/dashboard/salelist')
    //         setPurchaseList(data)
    //     }catch(error){
    //         message.error(error)
    //     }
    // }

    useEffect(()=>{
        let data = getPurchaseList({keyword,pageNumber,limit,setData:setPurchaseData,setLoading})
        setSuccess(false)
    },[success])

    const onFinish = (values) => {
        
        let data = {...values,
            product:selectedProduct._id,
            category:selectedProduct.category,
            productName:selectedProduct.name,
            purchaseDate: moment(values.purchaseDate),
            purchasePrice:parseFloat(values.purchasePrice),
            purchaseQty: parseInt(values.purchaseQty),
            purchaseShippingPrice: parseFloat(values.purchaseShippingPrice),
            remark:values.remark.trim(),
            supplier:{
                name:values.supplierName.trim(),
                tel:values.supplierTel.trim(),
                address:values.supplierAddress.trim()
            }
        }

        if(!isEdit){
            createPurchase({data,setSuccess,form})
            
        }else{
            editPurchase({editData:{...editData,...data},setSuccess})
            form.resetFields()
        }
        setIsEdit(false)
        // if(orderData.length > 0){
        //     console.log({
        //         ...values,
        //         orderItems:orderData, 
        //         taxPrice:values.taxPrice > 0 ? values.taxPrice:0,
        //         discount:values.discount > 0 ? values.discount :0,
        //         totalPrice:total
        //     })
        //     addOrder(
        //         {
        //             ...values,
        //             orderItems:orderData, 
        //             taxPrice:values.taxPrice > 0 ? values.taxPrice:0,
        //             discount:values.discount > 0 ? values.discount :0,
        //             totalPrice:total
        //         }
        //     );
        // }else{
        //     message.warning("No order!")
        // }

        
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleSelectProduct =(e)=>{
        let index = productData.findIndex(ed=> ed._id===e)
        setSelectedProduct(productData[index])
    }

    const handleEdit =(e)=>{   
        setIsEdit(true)
        setEditData(e)
        handleSelectProduct(e.product)
        form.setFieldsValue({
            ...e,
            purchaseDate:moment.parseZone(e?.purchaseDate),
            supplierName:e?.supplier?.name,
            supplierTel:e?.supplier?.tel,
            supplierAddress:e?.supplier?.address
        })
    }

    const handleDelete=(e)=>{
        // let id = e
        deletePurchase({id:e,setSuccess})
    }

    const handleCancel =()=>{
        form.resetFields()
        setIsEdit(false)
    }


    return (
        <>
            <Row style={{backgroundColor:'#F7F9FC',padding:10}}>
                <Col xs={24}>
                <Form
                    form={form}
                    name="addOrder"
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                    <Row>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="product"
                                rules={[{ required: true, message: 'Fields is required!' }]}
                            >
                                <Select placeholder="product" style={{ width: "100%" }} onChange={(e)=>handleSelectProduct(e)}>
                                    {
                                        productData?.map((e,index)=>(
                                            <Option value={e._id}>{e.name}</Option>
                                        ))
                                    }
                                
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="purchaseDate"
                                rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <DatePicker style={{width:'100%'}} placeholder="purchase date" />
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="purchaseQty"
                                rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input type="number" placeholder="qty" />
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="purchasePrice"
                                rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input type="number" placeholder="price $" />
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="purchaseShippingPrice"
                                // rules={[{ required: true, message: 'Field is required!' }]} 
                            >
                                <Input type="number" placeholder="shipping $" />
                            </Form.Item>
                        </Col>
                       

                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="remark"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="remark" />
                            </Form.Item>
                        </Col>

                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="supplierName"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="supplier" />
                            </Form.Item>
                        </Col>

                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="supplierTel"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="supplier tel" />
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="supplierAddress"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="supplier address" />
                            </Form.Item>
                        </Col>

                        <Col xs={4} style={{padding:5}}>
                            <Form.Item>
                                <Button
                                    style={{width:'100%'}}
                                    // onClick={()=> setOpenAddProduct(true)}
                                    htmlType="submit"
                                    className="btn-grad"
                                >
                                    {!isEdit ? "PURCHASE":"EDIT"}
                                </Button>
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item>
                                <Button
                                    // style={{backgroundColor:'#ff8b8c' ,color:'#FFFFFF'}}
                                    onClick={()=> handleCancel()}
                                    style={{width:'100%'}}
                                    type="danger"
                                    // disabled={orderData?.length<=0}
                                >
                                    CANCEL
                                </Button>
                            </Form.Item>
                        </Col>
                    </Row>

                </Form>
                </Col>
                <Col xs={24}>
                    <Table 
                        columns={purchaseColumn({handleEdit,handleDelete})} 
                        dataSource={purchaseData?.purchases} 
                        pagination={{
                            total: purchaseData?.paginator?.totalDocs,
                            //pageSizeOptions:["10", "20"],
                            // showSizeChanger: true,
                            pageSize:limit,
                            // current: purchaseData?.paginator?.currentPage,
                            onChange: ((page, pageSize) => { setPageNumber(page); setLimit(pageSize) })
                        }}
                        scroll={{ x: 1100 }} 
                        sticky 
                        loading={loading}
                    />
                </Col>
            </Row>
        </>
    )
}
