import React from 'react'
import {Space,Popconfirm,Button} from 'antd'
import {DeleteFilled ,EditFilled } from '@ant-design/icons'
import { currencyFormat, numberWithCommas } from '../../../fn/fn';
import { rielAsOne } from '../../../context/headerContext';
import moment from 'moment';

const ButtonGroup = Button.Group;

export default function purchaseColumn({handleEdit,handleDelete}) {
    var array = [
        {
            title: 'NÂ°',
            dataIndex: 'no',
            key: 'no',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: 'Product',
            dataIndex: 'productName',
            key: 'productName',
            width: 100,
        },
        {
            title: 'Category',
            dataIndex: 'category',
            key: 'category',
            width:80,
        },
        
        {
            title: 'Purchase Date',
            dataIndex: 'purchaseDate',
            key: 'purchaseDate',
            width: 80,
            render: (text, record) => (
                <span size="middle">
                    {moment.parseZone(record?.purchaseDate).format("DD-MMM-YYYY")}
                </span>
            )
            
        },
        {
            title: 'QTY',
            dataIndex: 'purchaseQty',
            key: 'purchaseQty',
            width: 40,
        },
       
        {
            title: 'Price',
            dataIndex: 'purchasePrice',
            key: 'purchasePrice',
            width: 130,
            render: (text, record) => (
                <span size="middle">
                    USD: {currencyFormat(record?.purchasePrice)}, KHRIEL: {numberWithCommas( record?.purchasePrice * rielAsOne)} &#6107;
                </span>
            )
            
        },

        {
            title: 'Shipping',
            dataIndex: 'purchaseShippingPrice',
            key: 'purchaseShippingPrice',
            width: 80,
            render: (text, record) => (
                <span size="middle">
                    {currencyFormat(record?.purchaseShippingPrice)}
                </span>
            )
            
        },

        {
            title: 'Supplier',
            dataIndex: 'supplier',
            key: 'supplier',
            width: 80,
            render: (text, record) => (
                <span size="middle">
                    {record?.supplier?.name}
                </span>
            )
            
        },

        {
            title: 'Remark',
            dataIndex: 'remark',
            key: 'remark',
            width: 100,
            render: (text, record) => (
                <span size="middle">
                    {record?.remark}
                </span>
            )
            
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 80,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Button className="link" style={{color:'#ad8b00'}} onClick={()=>handleEdit(record)}><EditFilled /></Button>
                    <Popconfirm
                        title="Do you want to remove this item?"
                        onConfirm={() => handleDelete(record._id)}
                        okText="Yes"
                        cancelText="No"
                        style={{cursor:'pointer'}}
                    >
                        <Button className="link" style={{ color: "red" }}><DeleteFilled /></Button>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}
