import { message } from 'antd';
import { API_LINK } from '../../../context/headerContext';

const axios = require('axios');

export const adJustStock =async ({data:d,setSuccess,form}) =>{

    try{
        await axios.post(API_LINK+`/api/product/${d?.product}/adjust`, d)
        setSuccess(true)
        form.resetFields()
    }catch(error){
        message.error(error.message+" eee")
    }
    
}