import React, { useEffect } from 'react'
import {Row,Col,Button,Card,Table,Image,Input,message, Form} from 'antd'
import orderlistColumn from './tableColumn/orderlistColumn'
import { useState } from 'react/cjs/react.development';
import EditOrder from './modal/EditOrder';
import { API_LINK } from '../../context/headerContext';

export default function OrderList({handlePrintInvoice}) {
    const axios = require('axios');

    const [orderList,setOrderList] = useState([])
    const [open,setOpen] = useState(false)
    const [success,setSuccess] = useState(false)
    const [editData,setEditData] = useState({})
    const [loading,setLoading] = useState(true)

    const getOrder = async()=>{
        try{
            let {data} = await axios.get(API_LINK+'api/order')
            if(data){
                setOrderList(data)
                setLoading(false)
            }
           
        }catch(error){
            message.error(error)
        }
    }

    const updateDelivery = async(e)=>{
        try{
            let {data} = await axios.put(API_LINK+`api/order/${e}/deliver`)
            // console.log(data)
            // setOrderList(data)
            console.log(data)
            if(data.success===true){
                setSuccess(true)
                message.success("Order is delivered!")
            }else{
                message.error(data.message)
            }
            
        }catch(error){
            message.error(error.message)
        }
    }

    const cancelOrder = async(e)=>{
        try{
            let {data} = await axios.put(API_LINK+`api/order/${e}/cancel`)
            if(data.success===true){
                setSuccess(true)
                message.info("Order is cancel!")
            }else{
                message.error(data.message)
            }
            
        }catch(error){
            message.error(error.message)
        }
    }

    useEffect(()=>{
        getOrder()
        setSuccess(false)
    },[success])

    const handleEdit=(e)=>{
        setEditData(e)
        setOpen(true)
    }

    const handleDeliver=(e)=>{
        updateDelivery(e._id)
        
    }

    const handleCancel =(e)=>{
        console.log(e)
        cancelOrder(e._id)
    }

    return (
        <>
            <EditOrder open={open} setOpen={setOpen} setSuccess={setSuccess} data={editData} />
            <Table 
                className="order-table"
                columns={orderlistColumn({handleEdit, handleDeliver,handlePrintInvoice,handleCancel})} 
                dataSource={orderList} 
                pagination={false}
                loading={loading}
            />
        </>
    )
}
