import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button,Select, message, InputNumber} from 'antd';
import { API_LINK } from '../../../context/headerContext';
import { ListSelect } from '../../../fn/own-comp';
const { Option } = Select;

export default function EditProduct({open,setOpen,setSuccess,productList}) {
    const axios = require('axios');
    let [form] = Form.useForm()

    const [selectedProduct, setSelectedProduct] = useState({})

    const editProduct =async (e) =>{
        try{
            await axios.put(API_LINK+`/api/product/${e.product}`, e)
            setSuccess(true)
            setOpen(false)
            form.resetFields()
        }catch(error){
            message.error(error.message)
        }
        
    }

    useEffect(()=>{
        form.resetFields()
        setSelectedProduct({})
    },[open])

    const onFinish = (values) => {
        editProduct(values)
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleSelectProduct =(e)=>{
        let index = productList.findIndex(ed=> ed._id===e)
        let product = productList[index]
        setSelectedProduct(productList[index])
        form.setFieldsValue({
            product:e,
            name:product.name,
            category:product.category,
            inStock:product.inStock,
            price:product.price,
            remark:product.remark,
            active:product.active
        })
    }

    return (
        <Modal
            title="EDIT PRODUCT"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addProduct"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{span:24}}>
                            <Form.Item
                                name="product"
                                rules={[{ required: true, message: 'Fields is required!' }]}
                            >
                             
                                <ListSelect title="product" data={productList} setValue={handleSelectProduct} />
                            </Form.Item>
                    </Col>

                    { 
                    selectedProduct?._id!==undefined ? (
                        <>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="name"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <Input placeholder="name" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="category"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <Select placeholder="category" style={{ width: "100%" }}>
                                <Option value="VEGETABLE">VEGETABLE</Option>
                                <Option value="MEAT">MEAT</Option>
                                <Option value="FRUIT">FRUIT</Option>
                                <Option value="CAKE">CAKE</Option>
                                <Option value="EGG">EGG</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="inStock"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <InputNumber min={0} placeholder="in stock" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="price"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <InputNumber min={0} placeholder="price $"  style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="active"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <Select placeholder="status">
                                <Option value={true} key="ACTIVE">ACTIVE</Option>
                                <Option value={false} key="INACTIVE">INACTIVE</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }} >
                        <Form.Item
                            name="remark"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="remark" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                            // onClick={()=> }
                        >
                            EDIT
                        </Button>
                    </Col>
                    </>):null}
                </Row>
            </Form>
        </Modal>
    )
}
