import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button,Select, message, InputNumber} from 'antd';
import { API_LINK } from '../../../context/headerContext';
import { ListSelect } from '../../../fn/own-comp';
import { adJustStock } from '../create/AdjustItem';
const { Option } = Select;

export default function AdjustStock({open,setOpen,setSuccess,productList}) {
    const axios = require('axios');
    let [form] = Form.useForm()

    const [selectedProduct, setSelectedProduct] = useState({})

    const onFinish = (values) => {
        values = {...values,
            actualStock:parseFloat(values.actualStock),
            newStock:parseFloat(values.newStock),
        }
        adJustStock({data:values,setSuccess,form})
        setOpen(false)
    };

    useEffect(()=>{
        form.resetFields()
        setSelectedProduct({})
    },[open])

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleSelectProduct =(e)=>{
        let index = productList.findIndex(ed=> ed._id===e)
        let product = productList[index]
        setSelectedProduct(productList[index])
        form.setFieldsValue({
            product:e,
            actualStock:product.inStock,
           
        })
    }

    return (
        <Modal
            title="STOCK ADJUSTMENT"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addProduct"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                                name="product"
                                rules={[{ required: true, message: 'Fields is required!' }]}
                            >
                             
                                <ListSelect title="product" data={productList} setValue={handleSelectProduct} />
                        </Form.Item>
                    </Col>

                    { 
                    selectedProduct?._id!==undefined ? (
                        <>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="actualStock"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <InputNumber disabled min={0} placeholder="in stock" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="newStock"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <InputNumber min={0} placeholder="new Stock"  style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="adjustStockType"
                            rules={[{ required: true, message: 'Fields is required!' }]}
                        >
                            <Select placeholder="type" style={{ width: "100%" }}>
                                <Option value={true}>áá¾ááááá¶</Option>
                                <Option value={false}>Adjust</Option>
                            
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }} >
                        <Form.Item
                            name="remark"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="remark" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                            // onClick={()=> }
                        >
                            SAVE
                        </Button>
                    </Col>
                    </>):null}
                </Row>
            </Form>
        </Modal>
    )
}
