import React, { useEffect, useState } from 'react'
import {Row,Col,Button,Card,Table,Image,Input,message, Form,Select,DatePicker} from 'antd'
import purchaseColumn from './tableColumn/stockColumn';
import moment from 'moment';
import { getPurchaseList } from '../purchase/get/getPurchase';
import { getStock } from './get/getStock';
import { numberWithCommas } from '../../fn/fn';
import { rielAsOne } from '../../context/headerContext';

const {Option} = Select
const { Column, ColumnGroup } = Table;

export default function Stock({productData}) {
    let [form] = Form.useForm()
    const axios = require('axios');

    const [stockData,setStockData] = useState([])
    const [open,setOpen] = useState(false)
    const [success,setSuccess] = useState(false)
    const [editData,setEditData] = useState({})

    const [keyword ,setKeyword] = useState("")
    const [pageNumber,setPageNumber] = useState(0)
    const [limit,setLimit]= useState(15)

    const [loading,setLoading] = useState(true)

    const [isEdit,setIsEdit] = useState(false)

    const [selectedProduct, setSelectedProduct] = useState({})

    // const getPurchaseList = async()=>{
    //     try{
    //         let {data} = await axios.get('api/dashboard/salelist')
    //         setPurchaseList(data)
    //     }catch(error){
    //         message.error(error)
    //     }
    // }

    useEffect(()=>{
        let data = getStock({keyword,pageNumber,limit,setData:setStockData,setLoading})
        setSuccess(false)
    },[success])

    const onFinish = (values) => {

        if(!isEdit){
            
        }else{
            form.resetFields()
        }

        
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleSelectProduct =(e)=>{
        let index = productData.findIndex(ed=> ed._id===e)
        setSelectedProduct(productData[index])
    }

    const handleEdit =(e)=>{   
        setIsEdit(true)
        setEditData(e)
        handleSelectProduct(e.product)
        form.setFieldsValue({
            ...e,
            purchaseDate:moment.parseZone(e?.purchaseDate),
            supplierName:e?.supplier?.name,
            supplierTel:e?.supplier?.tel,
            supplierAddress:e?.supplier?.address
        })
    }

    const handleDelete=(e)=>{
        // let id = e
        // deletePurchase({id:e,setSuccess})
    }

    const handleCancel =()=>{
        form.resetFields()
        setIsEdit(false)
    }


    return (
        <>
            <Row style={{backgroundColor:'#F7F9FC',padding:10}}>
                <Col xs={24}>
                <Form
                    form={form}
                    name="addOrder"
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                    <Row>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="product"
                                // rules={[{ required: true, message: 'Fields is required!' }]}
                            >
                                <Select placeholder="product" style={{ width: "100%" }} onChange={(e)=>handleSelectProduct(e)}>
                                    {
                                        productData?.map((e,index)=>(
                                            <Option value={e._id} key={e._id}>{e.name}</Option>
                                        ))
                                    }
                                
                                </Select>
                            </Form.Item>
                        </Col>
                        
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="date"
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <DatePicker style={{width:'100%'}} placeholder="date" />
                            </Form.Item>
                        </Col>

                        <Col xs={4} style={{padding:5}}>
                            <Form.Item>
                                <Button
                                    style={{width:'100%'}}
                                    // onClick={()=> setOpenAddProduct(true)}
                                    htmlType="submit"
                                    className="btn-grad"
                                >
                                    SEARCH
                                </Button>
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item>
                                <Button
                                    // style={{backgroundColor:'#ff8b8c' ,color:'#FFFFFF'}}
                                    onClick={()=> handleCancel()}
                                    style={{width:'100%'}}
                                    type="danger"
                                    // disabled={orderData?.length<=0}
                                >
                                    CANCEL
                                </Button>
                            </Form.Item>
                        </Col>
                    </Row>

                </Form>
                </Col>
                <Col xs={24}>
                    <Table 
                        // columns={purchaseColumn({handleEdit,handleDelete})} 
                        loading={loading}
                        dataSource={stockData} 
                        pagination={false}
                        // pagination={{
                        //     total: purchaseData,
                        //     //pageSizeOptions:["10", "20"],
                        //     // showSizeChanger: true,
                        //     pageSize:limit,
                        //     // current: purchaseData?.paginator?.currentPage,
                        //     onChange: ((page, pageSize) => { setPageNumber(page); setLimit(pageSize) })
                        // }}
                        scroll={{ x: 1100 }} 
                        sticky 
                    >
                        <Column title="NÂ°" dataIndex="no" key="no" width={70} render={
                            (text,record,i)=>(
                                <span>
                                    {i+1}
                                </span>
                            )
                        }/>
                        
                        <Column title="Date" dataIndex="purchaseDate" key="purchaseDate" />
                        <Column title="Product" dataIndex="productName" key="productName" />
                        <ColumnGroup className="merge-header" title={<b style={{ width:'100%',color:'#000000'}}>PURCHASE</b>} >
                            
                            <Column title="QTY" dataIndex="purchaseQty" key="purchaseQty" />
                            {/* <Column title="Price" dataIndex="purchasePrice" key="purchasePrice" /> */}
                            <Column title="Amount" dataIndex="totalPurchaseAmount" key="totalPurchaseAmount" 
                            render={
                                (text,record)=>(
                                    <span>
                                    {numberWithCommas(record?.totalPurchaseAmount * rielAsOne) } &#6107;
                                    </span>
                                )
                            }  
                            />
                        </ColumnGroup>

                        <ColumnGroup className="merge-header" title={<b style={{ width:'100%',color:'#000000'}}>SALE</b>}>
                            <Column title="QTY" dataIndex="totalSaleQty" key="totalSaleQty" />
                            {/* <Column title="Price" dataIndex="unitPrice" key="unitPrice" /> */}
                            <Column title="Amount" dataIndex="totalSaleAmount" key="totalSaleAmount" 
                             render={
                                (text,record)=>(
                                    <span>
                                    {numberWithCommas(record?.totalSaleAmount * rielAsOne) } &#6107;
                                    </span>
                                )
                            }  
                            />
                        </ColumnGroup>

                        <ColumnGroup className="merge-header" title={<b style={{ width:'100%',color:'#000000'}}>BALANCE</b>}>
                            <Column title="QTY" dataIndex="balanceQty" key="balanceQty" 
                             render={
                                (text,record)=>(
                                    <span>
                                    {record?.purchaseQty - record?.totalSaleQty}
                                    </span>
                                 )
                            }  
                            />
                            {/* <Column title="Price" dataIndex="balancePrice" key="balancePrice" /> */}
                            <Column title="Amount" dataIndex="balanceAmount" key="saleAmount"
                            render={
                                (text,record)=>(
                                    <span>
                                    { numberWithCommas((record?.totalPurchaseAmount - record?.totalSaleAmount)*rielAsOne)} &#6107;
                                    </span>
                                 )
                            }  
                            />
                        </ColumnGroup>

                    </Table>
                </Col>
            </Row>
        </>
    )
}
