import React, { useEffect, useState } from 'react'
import {Row,Col,Button,Card,Table,Image,Input,message, Form} from 'antd'
import salelistColumn from './tableColumn/salelistColumn'

export default function SaleList() {
    const axios = require('axios');

    const [saleList,setSaleList] = useState([])
    const [open,setOpen] = useState(false)
    const [success,setSuccess] = useState(false)
    const [editData,setEditData] = useState({})

    const [loading,setLoading]= useState(true)

    const getSaleList = async()=>{
        try{
            let {data} = await axios.get('api/dashboard/salelist')
            
            setSaleList(data)
            setLoading(false)
        }catch(error){
            message.error(error)
        }
    }

    useEffect(()=>{
        getSaleList()
    },[])

    return (
        <>
            <Table 
                columns={salelistColumn()} 
                dataSource={saleList} 
                pagination={false}
                loading={loading}
            />
        </>
    )
}
