import { message } from 'antd';

const axios = require('axios');

export const getStock = async({keyword,pageNumber,limit,setData,setLoading})=>{
    try{
        let {data} = await axios.get(`api/dashboard/stock`)
        // if(data.success===true){
        if(data){
            setData(data)
            setLoading(false)
        }
        // }
        // else{
        //     message.error(data.message)
        // }
        
    }catch(error){
        message.error(error)
    }
}