import { message } from 'antd';

const axios = require('axios');

export const deletePurchase = async({id,setSuccess})=>{
    try{
        let {data} = await axios.delete(`api/purchase/${id}`)
        if(data.success===true){
            setSuccess(true)
            message.info("Purchase has been deleted!")
        }else{
            message.error(data.message)
        }
        
    }catch(error){
        message.error(error)
    }
}