import React from 'react'
import { Select, Divider, Input, notification } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
const { Option } = Select;

export function ListSelect({ type, data, title, value, setValue, id, disabled, getNew }) {
    return(
        <Select
        showSearch
        style={{ width: "100%" }}
        placeholder={title}
        value={value}
        onChange={e => setValue(e)}
        disabled={disabled}
        optionFilterProp="children"
        filterOption={(input, option) =>
            option.children.indexOf(input) >= 0
        }
        filterSort={(optionA, optionB) =>
            optionA.children.localeCompare(optionB.children)
        }
    >
        {data.map(load => {
            return <Option value={load._id} key={load._id}>{load.name}</Option>
        })}
        </Select>
    )
}