import React from 'react'
import {Col,Button,Image, Divider} from 'antd'
import { currencyFormat, numberWithCommas } from '../../fn/fn'
import { rielAsOne } from '../../context/headerContext'
import {TagOutlined  } from '@ant-design/icons'

export default function MenuItem({item, handleClick}) {
    return (
        <Col xs={24} md={6} style={{padding:10}}>
            <Button style={{width:'100%',height:'100%'}} onClick={()=> handleClick(item)} disabled={item.inStock<=0}>
                <div style={{position:'relative'}}>
                    {/* <Image src={item.image} preview={false} width={"100px"} /> */}
                    <TagOutlined  style={{fontSize:100,color:'#CCCCCC'}} />
                    <div className="price-tag"><span style={{fontSize:14}}>{numberWithCommas(item.price * rielAsOne)}&#6107;</span><br/>{currencyFormat(item.price)}</div>
                </div>
                                        
                <span style={{textAlign:'center'}}>{item.name?.toUpperCase()}</span>
                <br/>
                <span style={{textAlign:'center'}}>{item.inStock > 0 ?"Stock: "+ item.inStock.toString()?.toUpperCase():"Not available"}</span>
            </Button>
        </Col>
    )
}
