import React, { useEffect, useRef, useState } from 'react'
import {Row,Col,Button,Card,Table,Image,Input,message, Form, Modal,Menu,Dropdown} from 'antd'
import orderColumn from './tableColumn/orderColumn';
import RedOnion from '../../assets/red-onion.jpg'
import MenuItem from '../menuItem/MenuItem';
import { currencyFormat, numberWithCommas } from '../../fn/fn';
import {PlusCircleOutlined ,ImportOutlined,InboxOutlined ,EditOutlined,UserOutlined} from '@ant-design/icons'
import AddProduct from '../product/modal/AddProduct';
import SaleList from '../salelist/SaleList';
import OrderList from '../order/OrderList';
import PrintInvoice from '../print/printInvoice';
import ReactToPrint from 'react-to-print';
import { rielAsOne } from '../../context/headerContext';
import { API_LINK } from '../../context/headerContext';
import Purchase from '../purchase/Purchase';
import Stock from '../stock/Stock';
import EditProduct from '../product/modal/EditProduct';
import AdjustStock from '../stock/modal/AdjustStock';

const { Meta } = Card;

export default function MenuPage() {
    
    let [form] = Form.useForm()

    const [openPrint, setOpenPrint] = useState(false)
    const componentRef = useRef()

    const [printData,setPrintData] = useState({})

    const axios = require('axios');
    const [success,setSuccess] = useState(false)

    const [saleListShow,setSaleListShow] = useState(false)
    const [saleShow,setSaleShow] = useState("")
    const [dashboardData,setDashboardData] = useState({})

    const [openAddProduct,setOpenAddProduct] = useState(false)
    const [openEditProduct,setOpenEditProduct] = useState(false)
    const [openAdjustStock,setOpenAdjustStock] = useState(false)

    const [orderData, setOrderData] = useState([])
    const [menuData, setMenuData] = useState([])
    const [totalOrderPrice, setTotalOrderPrice] = useState(0)

    const getProduct = async()=>{
        try{
            let {data} = await axios.get(API_LINK+'api/product')
            console.log(data,"not set")
            if(data){
                setMenuData(data)
            }
            
        }catch(error){
            console.log("error : "+ error)
            message.error(error.message)
        }
    }

    useEffect(() => {
        getProduct()
        setSuccess(false)
    }, [success,saleShow])

    const getDashBoard = async()=>{
        try{
            let {data} = await axios.get(API_LINK+'api/dashboard')
            if(data){
                setDashboardData(data)
            }
        }catch(error){
            console.log("error dash : "+ error)
            message.error(error.message)
        }
    }

    useEffect(() => {
        getDashBoard()
        setSuccess(false)
    }, [success])

    const handleAddOrder =(e)=>{
        let index = orderData.findIndex(ed=> ed._id===e._id)

        let newOrder = [...orderData,{_id:e._id,name:e.name,qty:1,price:e.price,product:e._id}]

        if(index> -1){
            let editOrder = {...orderData[index],qty:orderData[index].qty+1}
            // orderData.splice(index, 1,editOrder)
            // newOrder = [...orderData,editOrder] 
            newOrder = [...orderData.slice(0, index), editOrder,...orderData.slice(index + 1)]
        }
        setTotalOrderPrice(generateTotal(newOrder))
        setOrderData(newOrder)
    }

    const handleRemoveOrder = (e)=>{
        let index = orderData.findIndex(ed=> ed._id===e)
        orderData.splice(index, 1)
        setOrderData([...orderData])
        setTotalOrderPrice(generateTotal(orderData))
    }

    const handlePlusQty =(e)=>{
        let index = orderData.findIndex(ed=> ed._id===e._id)

        let editOrder = {...orderData[index],qty:orderData[index].qty+1}
        // orderData.splice(index, 1,editOrder)

        let newOrder = [...orderData.slice(0, index), editOrder,...orderData.slice(index + 1)]
        setTotalOrderPrice(generateTotal(newOrder))
        setOrderData(newOrder)
    }

    const handleEditQty =(e)=>{

        let index = orderData.findIndex(ed=> ed._id===e._id)
        // let newValue = orderData[index].newValue
        let editOrder = {...orderData[index],qty:e.newValue}
        // orderData.splice(index, 1,editOrder)

        let newOrder = [...orderData.slice(0, index), editOrder,...orderData.slice(index + 1)]
        setTotalOrderPrice(generateTotal(newOrder))
        setOrderData(newOrder)
    }


    const handleMinusQty =(e)=>{
        let index = orderData.findIndex(ed=> ed._id===e._id)

        let editOrder = {...orderData[index],qty:orderData[index].qty-1}
        // orderData.splice(index, 1)
        let newOrder = [...orderData.slice(0, index), editOrder,...orderData.slice(index + 1)]
        setTotalOrderPrice(generateTotal(newOrder))
        setOrderData(newOrder)
    }

    const handleCancelOrder =()=>{
        form.resetFields()
        setTotalOrderPrice(0)
        setOrderData([])
        message.warn("Order has cancelled!")
    }

    const handlePrintInvoice =(e)=>{
        setPrintData(e)
        setOpenPrint(true)
    }

    const generateTotal =(data)=>{
        let total = 0;
        data?.map((e,index)=>{
            total += e.price*e.qty
        })
        return total
    }

    const addOrder =async (e) =>{
        try{
            let {data}=await axios.post(API_LINK+'api/order', e)
            if(data.success===true){
                setSuccess(true)
                form.resetFields()
                setTotalOrderPrice(0)
                setOrderData([])
                message.success("Order has confirmed!")
            }else{
                message.error(data.message)
            }
            
        }catch(error){
            message.error(error.message)    
        }
        
    }

    const onFinish = (values) => {
        let total = generateTotal(orderData)

        if(orderData.length > 0){
            // console.log({
            //     ...values,
            //     orderItems:orderData, 
            //     taxPrice:values.taxPrice > 0 ? values.taxPrice:0,
            //     discount:values.discount > 0 ? values.discount :0,
            //     totalPrice:total
            // })
            addOrder(
                {
                    ...values,
                    orderItems:orderData, 
                    taxPrice:values.taxPrice > 0 ? values.taxPrice:0,
                    discount:values.discount > 0 ? values.discount :0,
                    totalPrice:total
                }
            );
        }else{
            message.warning("No order!")
        }

        
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    let totalSale = 0
    let totalSaleToday = 0
    if(dashboardData?.totalSale?.length>0 ){
        totalSale = dashboardData?.totalSale[dashboardData?.totalSale?.length-1]?.total
        totalSaleToday = dashboardData?.totalSaleToday?.length >0 ? dashboardData?.totalSaleToday[dashboardData?.totalSaleToday?.length-1]?.total:0
    }

    function handleButtonClick(e) {
        message.info('Click on left button.');
        console.log('click left button', e);
      }
      
      function handleMenuClick(e) {
        message.info('Click on menu item.');
        console.log('click', e);
      }

    const menu = (
        <Menu>
          <Menu.Item onClick={()=> setOpenAddProduct(true)} key="1">
            <button
                style={{width:'100%'}}
                className="btn-grad"
            >
                <PlusCircleOutlined /> NEW
            </button>
          </Menu.Item>
          <Menu.Item key="2" onClick={()=> setOpenEditProduct(true)}>
            <Button
                style={{width:'100%'}}
                // className="btn-grad"
                
            >
                <EditOutlined /> EDIT
            </Button>
          </Menu.Item>
          <Menu.Item key="3" onClick={()=> setOpenAdjustStock(true)}>
            <Button
                style={{width:'100%'}}
                // className="btn-grad"
                
            >
                <InboxOutlined /> Adjust Stock
            </Button>
          </Menu.Item>
        </Menu>
      );

    return (
        <>
            <Modal
                width={800}
                visible={openPrint}
                footer={[
                    // <Button
                    //     key="1"
                    //     onClick={() => setOpenPrintInvoice(true)}
                    // >
                    //     ááááááá·áááááááááááá
                    // </Button>,
                    <ReactToPrint
                        key="2"
                        trigger={() => <button type="primary" className="btn-grad">PRINT INVOICE</button>}
                        content={() => componentRef.current}
                    />
                ]}
                onCancel={() => setOpenPrint(false)}
            >
                <PrintInvoice 
                    ref={componentRef} 
                    printData={printData}
                />
            </Modal>

            <AddProduct open={openAddProduct} setOpen={setOpenAddProduct} setSuccess={setSuccess} />
            <EditProduct open={openEditProduct} setOpen={setOpenEditProduct} setSuccess={setSuccess} productList={menuData} />
            <AdjustStock open={openAdjustStock} setOpen={setOpenAdjustStock} setSuccess={setSuccess} productList={menuData} />

            <Row style={{width:'80%', margin:'0 auto'}}>
                <Col xs={24} md={8} style={{padding:10}}>
                    <Card
                        className="card-menu"
                        style={{height:150}}
                    >
                        <Row>
                            <Col xs={12}>
                                <h3>TOTAL PRODUCT</h3>
                                <div style={{fontSize:38}}>{dashboardData?.totalProduct >0  ? dashboardData?.totalProduct:0}</div>
                            </Col>
                            <Col xs={12}>

                                { saleShow !== "salelist"  ?
                                    <button
                                    className="btn-grad"
                                    onClick={()=> setSaleShow("salelist")}
                                    style={{width:'100%',backgroundColor:'#ff8b8c',color:"#FFFFFF"}}
                                    >
                                        Sale List
                                    </button>
                                :null} 
                            </Col>
                        </Row>
                    </Card>
                </Col>
                <Col xs={24} md={8} style={{padding:10}}>
                    <Card
                         className="card-menu"
                         style={{height:150}}
                    >
                        <Row >
                            <Col xs={12}>
                                <h3>TOTAL SALE</h3>
                                {/* <div style={{fontSize:38}}>{currencyFormat(totalSale)}</div> */}
                                <div style={{fontSize:38}}>{numberWithCommas(totalSale * rielAsOne)} &#6107;</div>
                            </Col>
                            <Col xs={12}>
                                { saleShow!== "orderlist" ?
                                <button
                                className="btn-grad"
                                onClick={()=> setSaleShow("orderlist")}
                                style={{width:'100%',backgroundColor:'#ff8b8c',color:"#FFFFFF"}}
                                >
                                    Order List
                                </button>
                                :null}
                                <br/><br/>
                                {/* <h3>Today: {currencyFormat(totalSaleToday)}</h3> */}
                                <h3>Today: {numberWithCommas(totalSaleToday * rielAsOne)} &#6107;</h3>

                                {/* { saleShow === false ?
                                <Button
                                // className="card-menu"
                                onClick={()=> setSaleShow(true)}
                                style={{width:'100%',backgroundColor:'#ff8b8c',color:"#FFFFFF"}}
                                >
                                    Sale List
                                </Button>
                                :null} */}
                            </Col>
                        </Row>
                        
                    </Card>
                </Col>
                <Col xs={24} md={8} style={{padding:10}}>
                    <Button
                     className="card-menu"
                     onClick={()=> setSaleShow("")}
                     style={{width:'100%',height:150,backgroundColor:'#ff8b8c', color:'#FFFFFF'}}
                    >
                        <h3 style={{color:'#FFFFFF'}}>NEW ORDER</h3>
                        <div style={{fontSize:38}}><PlusCircleOutlined /></div>
                    </Button>
                </Col>

                <Col xs={24} md={8} style={{padding:10}}>
                    <Button
                     className="card-menu"
                     onClick={()=> setSaleShow("purchase")}
                     style={{width:'100%',height:150,backgroundColor:'#ff8b8c', color:'#FFFFFF'}}
                    >
                        <h3 style={{color:'#FFFFFF'}}>PURCHASE</h3>
                        <div style={{fontSize:38}}><ImportOutlined /></div>
                    </Button>
                </Col>

                <Col xs={24} md={8} style={{padding:10}}>
                    <Button
                     className="card-menu"
                     onClick={()=> setSaleShow("stock")}
                     style={{width:'100%',height:150,backgroundColor:'#ff8b8c', color:'#FFFFFF'}}
                    >
                        <h3 style={{color:'#FFFFFF'}}>STOCK</h3>
                        <div style={{fontSize:38}}><InboxOutlined /></div>
                    </Button>
                </Col>

            </Row>
            <br/><br/>
            { saleShow ==="" ? 
            <Row >
                <Col xs={24} md={12} style={{backgroundColor:'#F7F9FC',padding:10}}>
                <Form
                    form={form}
                    name="addOrder"
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                    <Row>
                        <Col xs={6} style={{padding:5}}>
                            <Form.Item
                                name="invoiceID"
                                rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="#INVOICE" />
                            </Form.Item>
                        </Col>
                        <Col xs={6} style={{padding:5}}>
                            <Form.Item
                                name="client"
                                rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="customer" />
                            </Form.Item>
                        </Col>
                        <Col xs={12} style={{padding:5}}>
                            <Form.Item
                                name="shippingAddress"
                                rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="address" />
                            </Form.Item>
                        </Col>
                        <Col xs={3} style={{padding:5}}>
                            <Form.Item
                                name="taxPrice"
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input type="number" placeholder="tax %" />
                            </Form.Item>
                        </Col>
                        {/* <Col xs={4} style={{padding:5}}>
                            <Form.Item
                                name="shippingPrice"
                                // rules={[{ required: true, message: 'Field is required!' }]} 
                            >
                                <Input type="number" placeholder="shipping $" />
                            </Form.Item>
                        </Col> */}
                        <Col xs={3} style={{padding:5}}>
                            <Form.Item
                                name="discount"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input type="number" placeholder="dis %" />
                            </Form.Item>
                        </Col>

                        <Col xs={6} style={{padding:5}}>
                            <Form.Item
                                name="deliveredBy"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="deliver by" />
                            </Form.Item>
                        </Col>

                        <Col xs={6} style={{padding:5}}>
                            <Form.Item
                                name="clientTel"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="tel" />
                            </Form.Item>
                        </Col>

                        <Col xs={6} style={{padding:5}}>
                            <Form.Item
                                name="remark"
                                
                                // rules={[{ required: true, message: 'Field is required!' }]}
                            >
                                <Input placeholder="remark" />
                            </Form.Item>
                        </Col>

                        <Col xs={4} style={{padding:5}}>
                            <Form.Item>
                                <Button
                                    style={{width:'100%'}}
                                    // onClick={()=> setOpenAddProduct(true)}
                                    htmlType="submit"
                                    className="btn-grad"
                                >
                                    CONFIRM
                                </Button>
                            </Form.Item>
                        </Col>
                        <Col xs={4} style={{padding:5}}>
                            <Form.Item>
                                <Button
                                    // style={{backgroundColor:'#ff8b8c' ,color:'#FFFFFF'}}
                                    onClick={()=> handleCancelOrder()}
                                    style={{width:'100%'}}
                                    type="danger"
                                    disabled={orderData?.length<=0}
                                >
                                    CANCEL
                                </Button>
                            </Form.Item>
                        </Col>
                    </Row>

                </Form>

                    <Table 
                        columns={orderColumn({handleRemoveOrder, handlePlusQty,handleMinusQty,handleEditQty})} 
                        dataSource={orderData} 
                        pagination={false}
                    />
                    <h2 style={{textAlign:'right'}}>Total Price: {currencyFormat(totalOrderPrice)}</h2>
                    <h2 style={{textAlign:'right'}}>Riel: {numberWithCommas(totalOrderPrice * rielAsOne )} &#6107;</h2>
                </Col>

                <Col xs={24} md={12} style={{backgroundColor:'#F7F9FC'}}>
                    <Row>
                        <Col xs={24} md={24}>
                            <Row>
                            <Col xs={20} style={{padding:10}}>
                                <Input placeholder="Search for..." />
                            </Col>
                            <Col xs={2} style={{padding:10}}>
                                {/* <button
                                    style={{width:'100%'}}
                                    className="btn-grad"
                                    onClick={()=> setOpenAddProduct(true)}
                                >
                                    <PlusCircleOutlined /> NEW
                                </button> */}

                                <Dropdown.Button style={{width:'100%'}} overlay={menu}>
                                    {/* Dropdown */}
                                </Dropdown.Button>
                            </Col>

                            {/* <Col xs={6} style={{padding:10}}>
                                <Button
                                    style={{width:'100%'}}
                                    // className="btn-grad"
                                    onClick={()=> setOpenEditProduct(true)}
                                >
                                    <EditOutlined /> EDIT
                                </Button>
                            </Col> */}
                            </Row>
                        </Col>

                        <Col xs={24}>
                            <Row style={{maxHeight:500,overflowY:'scroll',width:'100%'}} >
                            { 
                            
                                menuData?.map((e,index)=>{
                                    return (
                                        <MenuItem item={e} handleClick={handleAddOrder} />
                                    )   
                                })
                            }
                        
                            </Row>
                        </Col>
                        
                    </Row>
                    
                    {/* <Card
                        hoverable
                        style={{ width: 100,height:100 }}
                        // cover={<img alt="example" src="https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png" />}
                    >
                        <Meta title="Europe Street beat" description="www.instagram.com" />
                    </Card> */}
                </Col>
            </Row>
            : null }

            {
            saleShow==="orderlist" ?
            <Row>
                <Col xs={24} style={{padding:10}}>
                    <OrderList handlePrintInvoice={handlePrintInvoice} />
                </Col>
            </Row>
            :null}

            {
            saleShow==="salelist" ?
            <Row>
                <Col xs={24} style={{padding:10}}>
                    <SaleList handlePrintInvoice={handlePrintInvoice} />
                </Col>
            </Row>
            :null}

            {
            saleShow==="purchase" ?
            <Row>
                <Col xs={24} style={{padding:10}}>
                    <Purchase productData={menuData} />
                </Col>
            </Row>
            :null}

            {
            saleShow==="stock" ?
            <Row>
                <Col xs={24} style={{padding:10}}>
                    <Stock productData={menuData} />
                </Col>
            </Row>
            :null}
        </>
    )
}
