import React from 'react'
import {Space,Popconfirm,Button} from 'antd'
import {DeleteOutlined, EditFilled ,CarFilled,PrinterFilled,StopFilled } from '@ant-design/icons'
import moment from 'moment'
import { currencyFormat, numberWithCommas } from '../../../fn/fn';
import { rielAsOne } from '../../../context/headerContext';

const ButtonGroup = Button.Group;

export default function orderlistColumn({handleEdit,handleDeliver,handlePrintInvoice,handleCancel}) {
    var array = [
        {
            title: 'NÂ°',
            dataIndex: 'no',
            key: 'no',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '#Invoice',
            dataIndex: 'invoiceID',
            key: 'invoiceID',
            width: 150,
        },
        {
            title: 'Date',
            dataIndex: 'createdAt',
            key: 'createdAt',
            width: 150,
            render: (text, record, i) => (
                <Space size="middle">
                    {moment(record.createdAt).format("DD-MMM-YYYY hh:mm:ss a")}
                </Space>
            ),
        },
        {
            title: 'Product',
            dataIndex: 'product',
            key: 'product',
            width: 100,
            render: (text, record, i) => (
                <Space size="middle">
                    {record?.orderItems?.length}
                </Space>
            ),
            
        },

        {
            title: 'Discount',
            dataIndex: 'discount',
            key: 'discount',
            width: 150,
            
        },

        {
            title: 'Tax',
            dataIndex: 'taxPrice',
            key: 'taxPrice',
            width: 150,
            
        },

        {
            title: 'Total Price',
            dataIndex: 'totalPrice',
            key: 'totalPrice',
            width: 100,
            render: (text, record, i) => (
                <span size="middle">
                    {/* {currencyFormat(record?.totalPrice)} */}
                    {numberWithCommas(record?.totalPrice * rielAsOne)}&#6107;
                </span>
            ),
        },

        {
            title: 'Payment',
            dataIndex: 'isPaid',
            key: 'isPaid',
            width: 100,
            render: (text, record, i) => {

                let badge = <span className="status-pending">Pending</span>

                if(record?.isDelivered===true){
                    badge=<span className="status-delivered">Delivered</span>
                }

                if(record?.isPaid===true){
                    badge=<span className="status-paid">Paid</span>
                }

                if(record?.isCancel===true){
                    badge=<span className="status-cancel">Cancel</span>
                }

                return (
                <Space size="middle" style={{width:'100%'}}>
                    {badge}
                </Space>
            )},
        },
       

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 50,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {/* <Link className="link" to={"/subCase/" + record.id}><EditFilled /></Link> */}
                    {/* <Popconfirm
                        title="Do you want to remove this item?"
                        onConfirm={() => handleRemoveOrder(record._id)}
                        okText="Yes"
                        cancelText="No"
                        style={{cursor:'pointer'}}
                    >
                        <Button className="link" style={{ color: "red" }}><DeleteOutlined /></Button>
                        
                    </Popconfirm> */}
                    <Popconfirm
                        title="Are you sure to confirm delivery?"
                        onConfirm={() => handleDeliver(record)}
                        okText="Yes"
                        cancelText="No"
                        style={{cursor:'pointer'}}
                        disabled={record?.isCancel}
                    >
                        <Button className="link" disabled={record?.isCancel} style={{color:'#1d39c4'}} ><CarFilled  /></Button>
                    </Popconfirm>
                    <Button className="link" style={{color:'#ad8b00'}} onClick={()=> handleEdit(record)} disabled={record?.isCancel}><EditFilled /></Button>
                    <Button className="link" onClick={()=> handlePrintInvoice(record)} disabled={record?.isCancel}><PrinterFilled /></Button>

                    <Popconfirm
                        title="Are you sure to cancel this order?"
                        onConfirm={() => handleCancel(record)}
                        okText="Yes"
                        cancelText="No"
                        style={{cursor:'pointer'}}
                        disabled={record?.isCancel}
                    >
                        <Button className="link" disabled={record?.isCancel} ><StopFilled /></Button>
                    </Popconfirm>
                </Space>
            ),
        }
    ]
    return array
}
